import { db } from '../../shared/firebase'
import { collection, getDocs, addDoc, setDoc, deleteDoc, updateDoc, doc } from 'firebase/firestore'
import { async } from '@firebase/util';


// 1. 
const LOAD_POST = 'post/LOAD_POST';
const MODIFY_POST = 'post/MODIFY_POST';



//3.   
export const loadPost = (post_List) => {
    return { type: LOAD_POST, post_List }
}

export const modityPost = (newPost) => {
    return { type:MODIFY_POST, newPost}
} 



// 2. 
const initialState = {
    postList: [],
}



//   ! 
export const loadPostFB = () => {
    return async function (dispatch) {
        const load = await getDocs(collection(db, "post"))
        let post_List = []
        load.forEach((l, idx) => {
            post_List.push({...l.data(), post_id:l.id})
        })

        dispatch(loadPost(post_List))
        
    }
}

//   
export const modifyPostFb = (state,id) => {    
    return async function (dispatch){
        const modify = await updateDoc(doc(db, "post", id), {
            ...state
          });
    }   

}
//   
export const deletePost = (id) => {
    return async function(dispatch){
        await deleteDoc(doc(db, "post", id));
    }

}



// 4.     
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case 'post/LOAD_POST': {
            return {...state,postList:action.post_List}
        }
        case 'post/MODIFY_POST':
            let modified_post = state.postList.map((post)=>
            post.id === action.post.id ? {...post, ...action.post} : post
            );
            return {
                ...state,
                postList:modified_post
            }
        default:
            return state;
    }
}



