import React, { useEffect, useRef, useState } from "react";
import { createUserWithEmailAndPassword, } from "firebase/auth";
import { db, auth } from '../shared/firebase'
import { collection, query, where, getDocs, addDoc } from 'firebase/firestore'
import { useNavigate } from 'react-router-dom'
import {Router} from 'react-router-dom'
import{signOut} from "firebase/auth"




const Signup = () => {

    const navigate = useNavigate()
    const name = useRef(null)
    const [email, setEmail] = useState("")
    const [pw, setPw] = useState("")
    const [dbUserId, setDbUserId] = useState("")
    
    
      
      



    const signupFB = async () => {

        const q = query(collection(db, "users"), where("user_id", "==", email));
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            setDbUserId(doc.data())
        });        

        //  ,
        // const user = await createUserWithEmailAndPassword(auth,email,pw)
        // .addOnCompleteListener(LoginActivity){
            
        // }
        

        const user_data = await addDoc((collection(db, "users")), {
            //    
            user_id: email,
            user_name: name.current.value
        })

        //  
        try {
            await createUserWithEmailAndPassword(auth, email, pw );   
            await signOut(auth);         
            return navigate('/login');
        }catch {
            alert("   ")
        }
    }

    // 

    const isEmail = (email) => {
        const emailRegex =
          /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    
        return (emailRegex.test(email))
      };


    //  
    const emailCH = () => {
        if (email === "") {
            return null
        } else if (isEmail(email) === true) {
            return true
        } else {
            return false
        }

    }

    // 
    const pwCH = () => {
        if (pw === "") {
            return null
        } else if (pw.length >= 8) {
            return true
        } else {
            return false
        }
    }



    return (
        <div className="signupContainer">
            <h1 className='title'></h1>
            <div className='signupContentBox'>
                <div className="con">
                    <p></p>
                    <input type="text" placeholder='' ref={name} />
                </div>
                <div className="con">
                    <p>( )</p>
                    <input type="text" onChange={(e) => { setEmail(e.target.value) }}  placeholder=' ?...'/>
                    {emailCH() || emailCH() === null ? null : <p className="RestrictionsMG">...........</p>}
                </div>
                <div className="con">
                    <p></p>
                    <form action=""><input type="password" onChange={(e) => { setPw(e.target.value) }} placeholder=' ' autoComplete="on"/></form>
                    {
                    pwCH() || pwCH() === null ? null : <p className="RestrictionsMG">8  8  8 !!!!!</p>
                }
                </div>
                {
                    emailCH() === true && pwCH() === true
                        ? <button style={{cursor:'pointer'}} onClick={() => {signupFB();}}></button>
                        : <button style={{opacity:'0.2'}}></button>
                }
                <p className="anotherBtn" onClick={()=>{
                    navigate('/login')
                }}>  </p>
                

            </div>


        </div>
    )
}

export default Signup